@import url(https://fonts.googleapis.com/css?family=Roboto:400,600|Work+Sans:400);
body {
  margin: 0;
  padding: 0;
  font-family: "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; }

* {
  list-style: none;
  text-decoration: none;
  padding: 0;
  margin: 0; }

body {
  background-color: #F9F4F4;
  font-family: Roboto, sans-serif; }

button {
  background: none;
  border: none;
  font-size: 1em;
  margin: 0 5px; }

.edit {
  color: green; }

.delete {
  color: red; }

.done {
  color: blue; }

.container {
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
  width: 100%;
  height: 100vh;
  min-height: 600px;
  max-width: 1200px;
  margin: auto;
  text-align: center;
  background-color: #9F9FAE;
  color: #271D2E; }
  .container header {
    width: 100%;
    background-color: #D25A37;
    height: 25vh; }
    .container header h1 {
      position: relative;
      top: 35%;
      color: #F9F4F4;
      font-family: "Work Sans", sans-serif;
      font-size: 3em; }
  .container .content {
    display: flex;
    flex-direction: column;
    height: 60vh; }
  .container .todo-input {
    width: 100%;
    flex-basis: 10%;
    margin-top: 30px;
    margin-bottom: 15px;
    display: flex;
    justify-content: center; }
    .container .todo-input label {
      margin-right: 5px;
      font-weight: 600; }
    .container .todo-input input {
      background-color: inherit;
      border: none;
      border-bottom: 1px #271D2E solid;
      margin-right: 5px;
      font: inherit; }
  .container .todo-edit {
    font-size: 0.9em;
    width: 90%;
    display: flex;
    flex-wrap: nowrap; }
    .container .todo-edit label {
      margin-right: 5px;
      font-weight: 600; }
    .container .todo-edit form {
      display: flex;
      border-bottom: 1px #271D2E solid;
      width: 100%; }
    .container .todo-edit input {
      background-color: inherit;
      border: none;
      margin-right: 5px;
      font: inherit;
      width: 100%; }
  .container .todo-list {
    width: 50%;
    margin: 0 auto; }
    .container .todo-list li {
      list-style: none;
      text-align: left;
      width: 100%;
      display: flex;
      line-height: 1.6em; }
      .container .todo-list li span {
        display: block;
        width: 90%; }
  .container footer {
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    bottom: 0;
    width: 100%;
    background-color: #271D2E;
    height: 15vh; }
    .container footer ul {
      display: flex;
      padding: 0;
      justify-content: space-around;
      width: 20%; }
      .container footer ul a {
        color: #F9F4F4;
        opacity: 0.8; }
      .container footer ul a:hover {
        opacity: 1; }

@media (max-width: 600px) {
  .container .todo-list {
    width: 80%; } }

